/* App.css */
*{margin: 0px; padding:0px; }
ul li {list-style: none;}
body{
  font-family: '맑은 고딕', Arial,sans-serif;
  font-size: 12px;
  color: #333;
  height: 100%;
  background-color: #eff3f7;
}
/* App.css */
.load_img{
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
}

h2{
  margin-bottom: 30px;
}


.nav{
  position: fixed;
  top: 200px;
  text-align: center;
}

.nav a{
  text-decoration: none;
  font-size: 16px;
  padding: 5px 10px;
  columns: #000;

}
@media screen and (min-width:768px) {
  .header{
    position: fixed;
    left: 0px; top: 0px;
    background: #ccc;
    width: 180px;
    height: 100vh;
  }
  .header > h1{
    text-align: center;
    padding: 15px 0px;
  }  }
  /* 모바일 */
@media screen and (max-width:767px) {
  .header{
    position: absolute;
    width: 100%; top: 0px;
  }
  .header h1{
    text-align: center;
  }  
}