@charset "UTF-8";

/* pc버전 */
@media screen and (min-width:768px) {
  .header{
    position: fixed;
    left: 0px; top: 0px;
    /* background: #ccc; */
    width: 180px;
    height: 100vh;
  }
  .header > h1{
    text-align: center;
    padding: 15px 0px;
  }  
  main{
    position: absolute;
    width: calc(width-180px);
    left: 180px;
    top: 0px;
    padding: 30px;
    box-sizing: border-box;
  }
}
.load_img.success {
  opacity: 0;
  visibility: hidden;
}


/* 모바일 */
@media screen and (max-width:767px) {
  .header{
    position: absolute;
    width: 100%; top: 0px;
  }
  .header h1{
    text-align: center;
  }  
}